function setNew(appStyles, setAppStyles, setAppClass, updateImage) {
  // because states dont reload if the undersource changes
  // we have to do that manualy
  // TODO: Any Better Way to do it?

  const colorMode = localStorage.getItem("VRA_COLOR_MODE");
  const qualityPref = localStorage.getItem("VRA_QUALITY_PREF");

  switch (colorMode) {
    case null:
    case "color":
      // color set specifically or never ever set mode
      const loadedBackgroundColor = localStorage.getItem("VRA_COLOR"); // load from localstorage
      if (
        loadedBackgroundColor === null &&
        appStyles?.backgroundColor !== "#282c34"
      ) {
        // if the loaded is null and we haven't set the background color
        // set the background color to default
        setAppStyles({ backgroundColor: "#282c34" });
        setAppClass("");
        return;
      }

      if (
        loadedBackgroundColor !== null &&
        appStyles?.backgroundColor !== loadedBackgroundColor
      ) {
        // if it changed to something different
        // console.log("setting new");
        setAppStyles({
          backgroundColor: localStorage.getItem("VRA_COLOR"),
        });
        setAppClass("");
      }
      break;

    case "gradient":
      setAppStyles({});
      setAppClass("gradient");
      break;

    case "paralax":
      setAppStyles({});
      setAppClass("paralaxTrees");
      break;

    case "unsplash":
      updateImage();
      break;
    case "scaleo":
      setAppStyles({});
      switch (qualityPref) {
        case "high":
          setAppClass("scaleoGifPreset scaleoGifHigh");
          break;

        case null:
        case "low":
          setAppClass("scaleoGifPreset scaleoGifLow");
          break;

        default:
          alert("?");
      }
      break;
    case "scaleoSecret":
      setAppStyles({});
      switch (qualityPref) {
        case "high":
          setAppClass("scaleoGifPreset scaleoSecretGifHigh");
          break;

        case null:
        case "low":
          setAppClass("scaleoGifPreset scaleoSecretGifLow");
          break;

        default:
          alert("?");
      }
      break;

    default:
      alert("Color Set Impossibly, Please Reset your Config");
  }
}

function cleanUp(mode, setAppStyles, setAppClass, setAttributionText) {
  switch (mode) {
    case null:
    case "color":
    case "gradient":
    case "paralax":
      setAppStyles({});
      setAppClass("");
      break;

    case "unsplash":
      setAppStyles({});
      setAttributionText(null);
      break;

    default:
      console.log("wait");
  }
}

export { setNew, cleanUp };
