import React from "react";
import { stob } from "../helpers";

function Misc(props) {
  return (
    <>
      <h2>Misc Settings</h2>
      <input
        type="checkbox"
        id="nextup"
        name="nextup"
        onChange={(e) => props.setDisplayNextUpWrap()}
        checked={
          localStorage.getItem("VRA_SHOW_NEXT") === null
            ? false
            : stob(localStorage.getItem("VRA_SHOW_NEXT"))
        }
      />
      <label htmlFor="color">Show Next Up</label>
    </>
  );
}

// dev note: browsers dont support bools in localstorage
// so it has to be cased into a string and backout into a bool

export default Misc;
