import { images } from "./defaults/images";

function getRandomImage() {
  return images[Math.floor(Math.random() * 30)];
}

function getParsedImage() {
  const image = getRandomImage();
  const parsed = {
    url: image.urls.full,
    attribution: image.user.username,
  };
  return parsed;
}

export { getParsedImage };
