import React, { useState } from "react";

function BackgroundSelect(props) {
  const [color, setColor] = useState(localStorage.getItem("VRA_COLOR"));
  const [selectedMode, setSelectedMode] = useState(
    localStorage.getItem("VRA_COLOR_MODE")
  );

  const handleColorChange = (e) => {
    const newColor = e.target.value;
    localStorage.setItem("VRA_COLOR", newColor);
    setColor(newColor);
    props.styleWraps.updateColorWrap();
  };

  const handleReset = () => {
    localStorage.setItem("VRA_COLOR", "#282c34");
    setColor("#282c34");
  };

  const handleModeChange = (mode) => {
    props.styleWraps.cleanUpWrap(selectedMode);
    localStorage.setItem("VRA_COLOR_MODE", mode);
    setSelectedMode(mode);
    props.styleWraps.updateColorWrap();
  };

  const selected = (mode) => {
    if (selectedMode === null) {
      // first ever load
      localStorage.setItem("VRA_COLOR_MODE", "color");
      setSelectedMode("color");
    }
    if (selectedMode === mode) {
      return true;
    }
    return false;
  };

  const qualitySelected = (mode) => {
    const pref = localStorage.getItem("VRA_QUALITY_PREF");
    if (pref === null) {
      // never set
      localStorage.setItem("VRA_QUALITY_PREF", "low");
      return mode === "low";
    }
    if (mode === pref) {
      return true;
    }
    return false;
  };

  const handleQualityChange = (e) => {
    localStorage.setItem("VRA_QUALITY_PREF", e.target.value);
    props.styleWraps.cleanUpWrap(selectedMode);
    props.styleWraps.updateColorWrap();
  };

  return (
    <>
      <div className="colorSelects">
        <h2 style={{ margin: "0px 0px 20px 0px" }}>Background</h2>
        <div className="colorSelect">
          <input
            type="radio"
            id="color"
            name="select"
            value="color"
            onChange={() => handleModeChange("color")}
            checked={selected("color")}
          />
          <label htmlFor="color">
            <div className="colorSelect">
              <input
                type="color"
                id="head"
                name="head"
                value={color === null ? "#282c34" : color}
                className="selector"
                onChange={handleColorChange}
              />
              <button onClick={handleReset}>Reset Color</button>
            </div>
          </label>
        </div>
        <h3>or</h3>
        <div className="colorSelect">
          <input
            type="radio"
            id="gradient"
            name="select"
            value="gradient"
            onChange={() => handleModeChange("gradient")}
            checked={selected("gradient")}
          />
          <label htmlFor="gradient">
            <p className="gradient gradient-text ">Gradient</p>
          </label>
        </div>
        <h3>or</h3>
        <div className="colorSelect">
          <input
            type="radio"
            id="paralax"
            name="select"
            value="paralax"
            onChange={() => handleModeChange("paralax")}
            checked={selected("paralax")}
          />
          <label htmlFor="paralax">
            <p>Paralax</p>
          </label>
        </div>
        <h3>or</h3>
        <div className="colorSelect">
          <input
            type="radio"
            id="unsplash"
            name="select"
            value="unsplash"
            onChange={() => handleModeChange("unsplash")}
            checked={selected("unsplash")}
          />
          <label htmlFor="unsplash">
            <p>Unsplash, Updates every 10 min</p>
          </label>
        </div>
        <h3>or</h3>
        <div className="colorSelect">
          <input
            type="radio"
            id="scaleo"
            name="select"
            value="scaleo"
            onChange={() => handleModeChange("scaleo")}
            checked={selected("scaleo")}
          />
          <label htmlFor="unsplash" style={{ margin: "0px 5px 0px 0px" }}>
            <p>Le Scaleo</p>
          </label>
          <select name="quality" id="quality" onChange={handleQualityChange}>
            <option value="low" selected={qualitySelected("low")}>
              Low (faster loads)
            </option>
            <option value="high" selected={qualitySelected("high")}>
              High (looks good)
            </option>
          </select>
        </div>
      </div>
    </>
  );
}

export default BackgroundSelect;
