const convertToEST = (time) => {
  if (time - 18000 < 0) {
    return time - 18000 + 86400; // time - 5hours (make it EST) and add a day (the date cannot be negative)
  }
  return time - 18000;
};

function validURL(str) {
  var pattern = new RegExp(
    "^(https?:\\/\\/)?" + // protocol
      "((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|" + // domain name
      "((\\d{1,3}\\.){3}\\d{1,3}))" + // OR ip (v4) address
      "(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*" + // port and path
      "(\\?[;&a-z\\d%_.~+=-]*)?" + // query string
      "(\\#[-a-z\\d_]*)?$",
    "i"
  ); // fragment locator
  return !!pattern.test(str);
}

function dayToString(day) {
  switch (day) {
    case "1":
      return "Monday";
    case "2":
      return "Tuesday";
    case "3":
      return "Wednesday";
    case "4":
      return "Thursday";
    case "5":
      return "Friday";
    case "any":
      return "Every Day";
    default:
      return "idk â†’ " + day;
  }
}

function parseLink(link) {
  if (link === null) {
    return null;
  }

  if (!link.includes("http")) {
    link = "http://" + link;
  }
  return link;
}
// period stuff
function calcRichContext(timeSlot) {
  // logic
  let loadedLink = null; // link shenanigans
  let prefix = ""; // display prefix
  let richContext = "";

  if (timeSlot.richContext !== undefined) {
    // regular context
    const loadedRC = localStorage.getItem("VRA_" + timeSlot.richContext);
    if (loadedRC !== null) richContext = ` | ${loadedRC}`;

    // during session link
    loadedLink = parseLink(
      localStorage.getItem("VRA_LINK_" + timeSlot.richContext)
    );
  }

  if (timeSlot.mRichContext) {
    // Transition Context
    const loadedFrom = localStorage.getItem(
      "VRA_" + timeSlot.mRichContext.from
    );
    const loadedTo = localStorage.getItem("VRA_" + timeSlot.mRichContext.to);
    if (loadedFrom !== null && loadedTo !== null) {
      richContext = ` | ${loadedFrom} â†’ ${loadedTo}`;
    }

    // transition link
    loadedLink = parseLink(
      localStorage.getItem("VRA_LINK_" + timeSlot.mRichContext.to)
    );
  }
  // set link
  if (loadedLink !== null) {
    // setLink(loadedLink);
    prefix = "ðŸ”— "; // custom prefixing
  }
  return { prefix: prefix, link: loadedLink, richContext: richContext };
}

function calculateClock(timesTable, isWeekend) {
  let newIsWeekend = "";
  let currentSlot = {};
  let nextSlot = {};

  const fromTheStartOfDay = convertToEST(
    Math.round((Date.now() / 1000) % 86400)
  );

  const dayToday = new Date().getDay().toString();

  if (dayToday === "6" || dayToday === "7") {
    return { newIsWeekend: true, newCurrentSlot: null, newNextSlot: null };
  } else {
    if (isWeekend) newIsWeekend = false;
  }

  if (!!timesTable || !typeof timesTable === "undefined") {
    // data ready
    const dayMap = [...timesTable["any"], ...timesTable[dayToday]]; // ES6 deconstruct 2 elements into an array
    let update = false;

    dayMap.forEach((period, i) => {
      if (
        period.start <= fromTheStartOfDay &&
        period.end >= fromTheStartOfDay
      ) {
        currentSlot = period;
        if (i !== dayMap.length - 1) {
          nextSlot = dayMap[i + 1];
        } else {
          nextSlot = null;
        }
        update = true;
      }
    });

    if (!update) {
      currentSlot = {
        start: 0,
        end: 86400,
        event: "Bug Encountered: Please send time and date to Vlad",
        primaryFocus: 1,
      };
    }
    return {
      newCurrentSlot: currentSlot,
      newNextSlot: nextSlot,
      newIsWeekend: newIsWeekend,
    };
  }
}

// string to bool
// f***ing javascript
function stob(s) {
  switch (s) {
    case "1":
    case "true":
    case true:
      return true;
    case "0":
    case "false":
    case null:
    case false:
      return false;
    default:
      throw new Error("invalid conversion, data: " + s);
  }
}

export {
  convertToEST,
  validURL,
  dayToString,
  parseLink,
  calcRichContext,
  calculateClock,
  stob,
};
